.container{
    background-color: #f9f9f9;;
    padding-left: 17%;
    padding-right: 2%;
    padding-top: 20px;
    padding-bottom: 20px;
}
.banner{
    width: 100%;
    margin-top: 60px;
}
.banner img{
    width: 100%;
    border-radius: 8px;
}
.list-container{
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(250px,1fr));
    margin-top: 15px;
}
.vid-list{
    padding: 10px;
}
.vid-list .thumbnail{
    width: 100%;
    border-radius: 5px;     
}
.vid-list .flex-div{
    align-items:flex-start;
    margin-top: 7px;
}
.flex-div{
    display: flex;
    align-items: center;
}
.vid-list .flex-div img{
    width: 35px;
    margin-right: 10px;
    border-radius: 50%;
}
.vid-info{
    color: #5a5a5a;
    font-size: 13px;
}
.vid-info a{
    color: #000;
    font-weight: 600;
    display: block;
    margin-bottom: 5px;
}
@media (max-width:900px) {
    .container{
        padding-left: 5%;
    }    
}